import React, {Component} from 'react';
import $ from 'jquery/dist/jquery.js';
window.jQuery = window.$ = $;
require('bootstrap');
import 'bootstrap/dist/css/bootstrap.css';
// import 'bootstrap/dist/js/bootstrap.js';
import 'font-awesome/css/font-awesome.css';
import './App.scss';
import data from './data.json';
import SubNav from './subnav';
import HeaderNav from './HeaderNav';
import Skills from './Skills';
import About from './About';
import Hobbies from './hobbies';
import Blog from './blog';

class App extends Component {

  render () {
    return (
      <div>
        <section id="home">
          <HeaderNav />
        </section>
        <section className="container position-relative" id="portfolio">              
          <SubNav showBlocks={data}/>
        </section>
        <section className="container-fluid sdm-bg" id="skills"> 
          <Skills />
        </section>
        <section className="container" id="about"> 
          <About />
        </section>
        <section className="container-fluid sdm-bg" id="Blog">
          <Blog />
        </section>
        <section id="hobbies">
          <div className="container about_me">
            <div className="col-md-6 col-md-offset-3 text-center">
              <h3><li className="fa fa-user"></li>About me</h3>
              <div className="block">
                <p>I am a passionate developer specialized in front-end who has been working in this field for over more than 3 years now.</p>
                <p>Next to that i also have a keen eye for User Experience and interaction design which results in a great online experience.</p>
                <p>When i'm not at work i like to create games, apps or web applications.</p>
              </div>
            </div>
          </div>
          <div className="container-fluid sdm-bg">
            <Hobbies />
          </div>
        </section>
      </div>
    )
  }
}
export default App;



