import React, { Component } from 'react';
import Scrollspy from 'react-scrollspy';
import $ from 'jquery';

class HeaderNav extends Component {
    state = {
        test: ''
    };

        scrollMeTo = (event, id) => {
            let element = document.getElementById(id);
            // Make sure this.hash has a value before overriding default behavior
            if (element !== "") {

                // Prevent default anchor click behavior
                event.preventDefault();
                
                // Using jQuery's animate() method to add smooth page scroll
                // The optional number (800) specifies the number of milliseconds it takes to scroll to the specified area
                $('html, body').animate({
                    scrollTop: id === 'portfolio' ? element.offsetTop + 55 : element.offsetTop
                });
            }
        }
    render() {
        window.addEventListener('scroll', (event) => {
            let scroll = event.srcElement.body.scrollTop;
            if (this.state.test === '') {
                if (scroll >= 100) {
                    this.setState({ test: 'scroll-down' });
                }
            } else if (this.state.test === 'scroll-down') {
                if (scroll < 100) {
                    this.setState({ test: '' });
                }
            }
        });
        return (
            <header className="container-fluid sdm-bg">
                <div className="row">
                    <div className="overlay"></div>
                    <Scrollspy items={['home', 'portfolio', 'skills', 'about', 'Blog', 'hobbies']} offset={-55}
                        className={'nav nav-pills menu-center margin-top-30 scroll-nav ' + this.state.test}
                        currentClassName="active-menu"
                        onEvent={(e) =>this.scrollMeTo(e)}>
                        <li role="presentation">
                            <a href="#home" onClick={(e) =>this.scrollMeTo(e, 'home')}><i className="fa fa-home"></i></a>
                        </li>
                        <li role="presentation">
                            <a href="#portfolio" onClick={(e) =>this.scrollMeTo(e, 'portfolio')}>Portfolio</a>
                        </li>
                        <li role="presentation">
                            <a href="#skills" onClick={(e) =>this.scrollMeTo(e, 'skills')}>skills</a>
                        </li>
                        <li role="presentation">
                            <a href="#about" onClick={(e) =>this.scrollMeTo(e, 'about')}>About</a>
                        </li>
                        <li role="presentation">
                            <a href="#Blog" onClick={(e) =>this.scrollMeTo(e, 'Blog')}>Blog</a>
                        </li>
                        <li role="presentation">
                            <a href="#hobbies" onClick={(e) =>this.scrollMeTo(e, 'hobbies')}>hobbies</a>
                        </li>
                    </Scrollspy>
                    <img src="images/profile2.jpg" className="margin-top-76 img-circle center-block profile_picture" alt="profile" />
                    <h1 className="text text-center">Eric v/d Helm</h1>
                    <h3 className="text-capitalize text-center">Creative front-end enthousiast</h3>
                    <hr />
                </div>
                <div className="text-center margin-bottom-135">
                    <ul className="social-networks spin-icon">
                        <li><a href="https://www.facebook.com/eric.vanderhelm.7" className="icon-facebook"></a></li>
                        <li><a href="https://nl.linkedin.com/in/eric-van-der-helm-a82b412b" className="icon-linkedin"></a></li>
                        <li><a href="https://www.instagram.com/_0_helmpie_0_" className="icon-instagram"></a></li>
                    </ul>
                </div>
            </header>
        )
    }
}


export default HeaderNav;


