import React, {Component} from 'react';
import LazyLoad from 'react-lazy-load';


class Skills extends Component {
    render() {
        return(
           <div className="row">
                <div>
                    <h3 className="text-center">Skills</h3>
                </div>
                <LazyLoad height={280} offsetTop={10}>
                <section className="container">
                    <div className="row">
                        <div className="col-md-6 col-xs-6">
                            <div className="center-block fixed-block">
                                <h4>Languages</h4>
                                <div className="circleContainer center-block">
                                    <div className="circleMain">
                                        <em>NL</em>
                                    </div>
                                    <div className="circle1">
                                        <em>DE</em>
                                    </div>
                                    <div className="circle2">
                                        <em>EN</em>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="tools col-md-6 col-xs-6">
                            <h4>Practice Areas</h4>
                            <ul>
                                <li className="percentage percentage-80"><span className="text">Agile</span></li>
                                <li className="percentage percentage-90"><span className="text">Scrum</span></li>
                                <li className="percentage percentage-70"><span className="text">User Experience</span></li>
                                <li className="percentage percentage-80"><span className="text">Front-end development</span></li>
                                <li className="percentage percentage-35"><span className="text">Back-end Development</span></li>
                                <li className="percentage percentage-25"><span className="text">Web Design</span></li>
                                <li className="percentage percentage-25"><span className="text">3D animation</span></li>
                                <li className="percentage percentage-35"><span className="text">Object modeling</span></li>
                            </ul>
                        </div>
                    </div>
                    <div className="row">
                        <div>
                            <h4 className="text-center">Technical skills</h4>
                        </div>
                        <div className="col-md-2 col-xs-4">
                            <figure className="chart chart-left-right" data-percent="50">
                                <figcaption className="chart-title">Java</figcaption>
                                <svg width="100" height="100">
                                    <circle className="outer-back" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>    
                                    <circle className="outer" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>
                                </svg>
                            </figure>
                        </div>
                        <div className="col-md-2 col-xs-4">
                            <figure className="chart chart-left-right" data-percent="100">
                                <figcaption className="chart-title">Angularjs</figcaption>
                                <svg width="100" height="100">
                                    <circle className="outer-back" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>    
                                    <circle className="outer" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>
                                </svg>
                            </figure>
                        </div>
                        <div className="col-md-2 col-xs-4">
                            <figure className="chart chart-left-right" data-percent="75">
                                <figcaption className="chart-title">Gulp</figcaption>
                                <svg width="100" height="100">
                                    <circle className="outer-back" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>    
                                    <circle className="outer" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>
                                </svg>
                            </figure>
                        </div>
                        <div className="col-md-2 col-xs-4">
                            <figure className="chart chart-left-right" data-percent="50">
                                <figcaption className="chart-title">CSS</figcaption>
                                <svg width="100" height="100">
                                    <circle className="outer-back" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>    
                                    <circle className="outer" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>
                                </svg>
                            </figure>
                        </div>
                        <div className="col-md-2 col-xs-4">
                            <figure className="chart chart-left-right" data-percent="25">
                                <figcaption className="chart-title">Android</figcaption>
                                <svg width="100" height="100">
                                    <circle className="outer-back" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>    
                                    <circle className="outer" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>
                                </svg>
                            </figure>
                        </div>
                        <div className="col-md-2 col-xs-4">
                            <figure className="chart chart-left-right" data-percent="75">
                                <figcaption className="chart-title">Javascript</figcaption>
                                <svg width="100" height="100">
                                    <circle className="outer-back" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>    
                                    <circle className="outer" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>
                                </svg>
                            </figure>
                        </div>
                    </div>
                     <div className="row">
                        <div className="col-md-2 col-xs-4">
                            <figure className="chart chart-right-left" data-percent="50">
                                <figcaption className="chart-title">Angular</figcaption>
                                <svg width="100" height="100">
                                    <circle className="outer-back" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>    
                                    <circle className="outer" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>
                                </svg>
                            </figure>
                        </div>
                        <div className="col-md-2 col-xs-4">
                            <figure className="chart chart-right-left" data-percent="50">
                                <figcaption className="chart-title">Reactjs</figcaption>
                                <svg width="100" height="100">
                                    <circle className="outer-back" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>    
                                    <circle className="outer" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>
                                </svg>
                            </figure>
                        </div>
                        <div className="col-md-2 col-xs-4">
                            <figure className="chart chart-right-left" data-percent="75">
                                <figcaption className="chart-title">jQuery</figcaption>
                                <svg width="100" height="100">
                                    <circle className="outer-back" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>    
                                    <circle className="outer" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>
                                </svg>
                            </figure>
                        </div>
                        <div className="col-md-2 col-xs-4">
                            <figure className="chart chart-right-left" data-percent="100">
                                <figcaption className="chart-title">HTML</figcaption>
                                <svg width="100" height="100">
                                    <circle className="outer-back" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>    
                                    <circle className="outer" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>
                                </svg>
                            </figure>
                        </div>
                        <div className="col-md-2 col-xs-4 last-chart">
                            <figure className="chart chart-right-left" data-percent="25">
                                <figcaption className="chart-title">Nodejs</figcaption>
                                <svg width="100" height="100">
                                    <circle className="outer-back" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>    
                                    <circle className="outer" cx="140" cy="50" r="35" transform="rotate(-90, 95, 95)"/>
                                </svg>
                            </figure>
                        </div>
                    </div>
                </section>
                </LazyLoad>
           </div>
        )
    }
}

export default Skills;


