import React, {Component} from 'react';
import LazyLoad from 'react-lazy-load';
import bats from './bats.json'


const aboutList = bats;
class About extends Component {
    state = {showButton: true, toggle: "work"}
    showAllItems = () => {
        aboutList.forEach(function (obj) {
            obj.show = true;
        });
        this.setState({showButton: false});
    }

    toggleButton = (state) => {
        this.setState({toggle: state});
    }
    getToggle = (state) => {
        return (this.state.toggle === state ? 'active btn btn-orange-test' : 'active btn btn-info');
    };
    getIcon = () => {
        return (this.state.toggle === "work" ? 'fa fa-briefcase icon' : 'fa fa-graduation-cap icon');
    };
    render() {
        return(
            <div className="row">
                 <LazyLoad height={300} offsetTop={10}>
                <div className="col-md-10 col-md-offset-1">
                    <div className="btn-group buttons-above-timeline">
                        <button type="button" className={this.getToggle('work')} onClick={()=> this.toggleButton('work')}>Work</button>
                        <button type="button" className={this.getToggle('study')} onClick={()=> this.toggleButton('study')}>Study</button>
                    </div>
                    <ul className="timeline">
                         {aboutList.map((object, index) => 
                            {if(object.show && object.section === this.state.toggle){
                            return <li key={index}>
                                <div className="tl-icon">
                                    <i className={this.getIcon()}></i>
                                </div>
                                <div className="timelabel">
                                    <h3>{object.title}</h3>
                                    <div className="date">
                                        <i className="fa fa-calendar"></i>
                                        {object.date}
                                    </div>
                                    <h4>{object.subTitle}</h4>
                                    <p>{object.detail}</p>
                                </div>
                            </li>}return null}
                         )}
                    </ul>
                      {this.state.showButton ? (
                        <button className="btn btn-orange" onClick={()=>this.showAllItems()} >Load more</button>
                        ) : (
                            ('')
                    )}
                </div>
                </LazyLoad>
            </div>
        )
    }
}

export default About;


