 import React, {Component} from 'react';
 import LazyLoad from 'react-lazy-load';
 
 class Hobbies extends Component {

  render () {
    return (
        <div className="row hobbies">
            <div className="overlay"></div>
            <div className="row text-center">
                <h3>Hobbies</h3>
                <h4>Out of work, here's what i enjoy the most!</h4>
            </div>
            <section className="container">
            <LazyLoad height={310} offsetTop={10}>
                <div className="row">
                    <div className="col-sm-3 col-xs-6">
                        <div className="block">            
                        <h3><i className="fa fa-android"></i>Crossfit</h3>
                        <p>Started with crossfit about a year ago and enjoyed it as much since.</p>
                        </div>
                    </div>
                    <div className="col-sm-3 col-xs-6">
                        <div className="block">
                        <h3><i className="fa fa-camera"></i>Photo's</h3>
                        <p>Proud owner of hx400v. I really enjoy making nice pictures while traveling</p>
                        </div>
                    </div>
                    <div className="col-sm-3 col-xs-6">
                        <div className="block">
                        <h3><i className="fa fa-globe"></i>traveling</h3>
                        <p>I like to travel. Travel as much as i can when i can.</p>
                        </div>
                    </div>
                    <div className="col-sm-3 col-xs-6">
                        <div className="block">
                        <h3><i className="fa fa-gamepad"></i>Gaming</h3>
                        <p>Whenever i got some spare time, i like to play some games like diablo</p>
                        </div>
                    </div>
                </div>
            </LazyLoad>
            </section>
        </div>
    )}}
    export default Hobbies;


